.Header{margin:5% 0;width:100%;min-height:400px;max-height:-webkit-max-content;max-height:-moz-max-content;max-height:max-content;color:#fff;display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;align-items:center}.Header div{font-size:30px;width:50%}.Header div button{background-color:#fde74c;border-radius:30px;border:0;border-top:0;border-bottom:0;padding:3%;transition:0.1s;cursor:pointer}.Header div button:hover{color:#e7312b}.Header img{width:350px;height:350px}@media screen and (max-width: 660px){.Header{flex-direction:column}.Header div{font-size:20px}.Header img{width:250px;height:250px}}

.Info {
  background-color: #fff;
  width: 100%;
  min-height: 400px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  padding: 4%;
  color: #1b1b1b;
}

.Info h1 {
  text-align: center;
}

.Info h1 span {
  color: #e7312b;
}

.Info i {
  font-size: 12px;
  color: grey;
}

.Info .how {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.Info .steps {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Info .steps span {
  margin: 20px;
  background-color: #e7312b;
  padding: 20px;
  border-radius: 50%;
  color: #fff;
