.About {
  color: #fff;
}

.About .intro {
  margin: 0 auto;
  width: 100%;
  min-height: 300px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  align-items: center;
  flex-wrap: wrap;
  font-size: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.About .intro .left {
  width: 70%;
}

.About .facts {
  width: 100%;
  min-height: 250px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  background-color: #e7312b;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.About .facts .fact {
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 20%;
}

.About .facts .fact span {
  background-color: #fff;
  border-radius: 50px;
  color: #e7312b;
  width: 70px;
  text-align: center;
  padding: 15px;
}

.About .why {
  background-color: #fff;
  color: #1b1b1b;
  width: 100%;
  min-height: 450px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
}

.About .why h1 {
  padding: 20px;
  text-align: center;
}

.About .why h1 span {
  color: #e7312b;
}

.About .why .reasons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.About .why .reason {
  color: #1b1b1b;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 20%;
}

.About .why .reason span {
  background-color: #e7312b;
  border-radius: 50px;
  color: #fff;
  width: 70px;
  text-align: center;
  padding: 15px;
  margin: 10px;
}

.About .why button {
  background-color: #fde74c;
  border-top: 0;
  border-bottom: 0;
  border-radius: 50px;
  border: 0;
  padding: 2%;
  font-weight: 900;
  margin: 5% auto;
  display: block;
}

@media screen and (max-width: 690px) {
  .why {
    font-size: 12px;
  }
}
